  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
}

.container:hover .blurImage{
    opacity:0;
    transition: .5s ease-in; 
}
.text {
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
  color:rgb(0,0,0,.7);
}


